.keyboard-simulator {
  min-height: 55px; }
  .keyboard-simulator__text {
    font-size: 24px;
    text-transform: uppercase;
    color: #fff;
    font-weight: bold; }
  .keyboard-simulator__inputing {
    color: #1db954; }
  .keyboard-simulator__cursor {
    margin-left: 2px;
    margin-bottom: -3px;
    width: 10px;
    height: 25px;
    background-color: #1db954;
    display: inline-block; }
    .keyboard-simulator__cursor--idle {
      -webkit-animation: blink 2s step-start 0s infinite;
              animation: blink 2s step-start 0s infinite; }

@-webkit-keyframes blink {
  50% {
    opacity: 0.0; } }

@keyframes blink {
  50% {
    opacity: 0.0; } }



